<template>
	<div class="create">
		<!-- <mt-header title="家人" class="createName">
			<router-link to="/" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
			<router-link to="/addCall" slot="right">
				<span><img src="../../assets/addicon.png" alt="add" width="20" height="20"></span>
			</router-link>
		</mt-header> -->
		<div class="focus">
			<mt-cell v-for="item in focusForm" :title="item.appellation" v-if="item.whether!=2"  :is-link="item.whether==1?true:false" :to="{name:'familyData',params:{id:item.follow_id,title:item.appellation+'的健康数据'}}">
				<p v-if="item.whether==0">请求关注</p>
	  			<mt-button class="focus_btn" type="danger" size="small" color="#A21A28" v-if="item.whether==0" @click.native="focusClick(item)" slot="right">关注</mt-button>
	  			<mt-button class="remove_btn" type="default" size="small" v-if="item.whether==0" slot="right" @click.native="removeClick(item)">忽略</mt-button>
	  			<span v-if="item.whether==1" slot="right">
	  				<i class="iconfont icon-taoxin" style="color:#A21A28;font-size:20px;"></i>
	  			</span>
	  			<img class="photo" slot="icon" :src="item.wx_avatar" v-if="item.wx_avatar">
	  			<img class="photo" slot="icon" src="../../assets/photo.png" v-if="!item.wx_avatar">
			</mt-cell>
		</div>
		<div class="next save">
			<router-link to="/addCall">
				<mt-button class="next-btn">添加关注</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			this.getFocusForm();
			return {
				focusForm: []
			}
		},
		methods: {
			getFocusForm: function () {
				//关注请求列表接口
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/list_focu?'+location.search.substring(1).split("&")[0]);
				resource.get().then((response) => {
					this.focusForm=response.data;
					//whether:处理结果0未处理1同意2忽略
				},(response) => {
					this.$toast(response.data.error);
				});
			},
			focusClick: function (item) {
				// 处理关注请求接口
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/problems_focu?'+location.search.substring(1).split("&")[0]);
				resource.save({focu_id:item.id,whether:1}).then((response) => {
					item.whether=1;
				}, (response) => {
					this.$toast('提交失败');
				})
			},
			removeClick: function (item) {
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/problems_focu?'+location.search.substring(1).split("&")[0]);
				resource.save({focu_id:item.id,whether:2}).then((response) => {
					item.whether=2;
				}, (response) => {
					this.$toast('提交失败');
				});
			}
		}
	}
</script>
<style lang="scss">
.focus{
	padding: 10px;
	.mint-cell{
		border-bottom:1px solid #cccccc;
		.mint-cell-allow-right{
			display: none;
		}
	}

}
.focus .mint-cell-title{
	width: 130px !important;
}
.focus .mint-cell-right button{
	margin-left: 10px;
	height: 26px;
	padding: 0 9px;
	font-size: 13px;
}
.focus .mint-cell-value p{
	position: absolute;
	left: 45px;
	font-size: 14px;
	top: 20px;
}
.focus .mint-cell-right{
	right: 115px;
	top: 10px;
}
.focus .mint-cell-right .focus_btn{
	background-color: #A21A28;
}
.focus .mint-cell-right .remove_btn{
	background-color: #fff;
	border: 1px solid #000;
}
.focus .mint-cell-right span{
	position: absolute;
	right: -100px;
}
.photo{
	width: 32px;
	height: 32px;
	border-radius: 50%;
	background-color: #f0f0f0;
}

</style>



