<template>
	<div class="create">
		<!-- <mt-header title="个人信息" class="seeInfo">
			<router-link to="/" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link> -->
			<!-- <router-link to="/editInfo" slot="right" class="edit">编辑</router-link> -->
		<!-- </mt-header> -->
		<div class="inform-photo">
			<img :src="informateList.wx_avatar" slot="center">
		</div>
		<table class="informate-tab" v-cloak>
			<tr >
				<td>姓名</td>
				<td>{{informateList.truename}}</td>
			</tr>
			<tr>
				<td>性别</td>
				<td>{{informateList.sex | sexFilter}}</td>
			</tr>
			<tr>
				<td>年龄</td>
				<td>{{informateList.age}}</td>
			</tr>
			<tr>
				<td>民族</td>
				<td>{{informateList.nation}}</td>
			</tr>
			<tr>
				<td>证件类型</td>
				<td>{{informateList.id_type | id_typeFilter}}</td>
			</tr>
			<tr>
				<td>证件号码</td>
				<td>{{informateList.id_code}}</td>
			</tr>
			<tr>
				<td>血型</td>
				<td>{{informateList.blood_type | blood_typeFilter}}</td>
			</tr>
			<tr>
				<td>子女数</td>
				<td>{{informateList.children}}</td>
			</tr>
			<tr>
				<td>兴趣爱好</td>
				<td>{{informateList.hobby_list}}</td>
			</tr>
			<tr>
				<td>特长</td>
				<td>{{informateList.speciality_list}}</td>
			</tr>
			<tr>
				<td>学历</td>
				<td>{{informateList.education | educationFilter}}</td>
			</tr>
			<tr>
				<td>职业</td>
				<td>{{informateList.job_list}}</td>
			</tr>
			<tr>
				<td>职务</td>
				<td>{{informateList.duty}}</td>
			</tr>
			<tr>
				<td>职称</td>
				<td>{{informateList.skill_level_list}}</td>
			</tr>
		</table>
		<div class="next save">
			<mt-button class="next-btn" @click="editClick">编辑</mt-button>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				informateList:{}
			}
		},
		created (){
			this.informateList = JSON.parse(window.localStorage.memberList);

		},
		filters: {
			sexFilter:function (input) {
				switch(input){
					case 1: return '女';
					case 0: return '男';
				}
			},
			id_typeFilter:function (input) {
				switch(input){
					case 0: return '身份证';
					case 1: return '护照';
					case 2: return '军官证';
					case 3: return '其他';
				}
			},
			blood_typeFilter:function (input) {
				switch(input){
					case 0: return 'A型';
					case 1: return 'B型';
					case 2: return 'AB型';
					case 3: return 'O型';
					case 4: return '其他';
				}
			},
			educationFilter:function (input) {
				switch(input){
					case 0: return '小学';
					case 1: return '初中';
					case 2: return '高中';
					case 3: return '学士';
					case 4: return '硕士';
					case 5: return '博士';
				}
			}
		},
		methods: {
			editClick (){
				window.localStorage.informateList = JSON.stringify(this.informateList);
				this.$router.push('/editInfo');
			}
		}
	}
</script>
<style>
	.inform-photo{
		width: 65px;
		/*height: 65px;*/
		margin: 0 auto;
		margin-top: 20px;
		overflow: hidden;
	}
	.inform-photo img{
		width: 65px;
		height: 65px;
		border-radius: 50%;
		background-color: #f0f0f0;
	}
	.informate-tab{
		margin: 10px 20px;
	}
	.informate-tab tr{
		line-height: 40px;
	}
	.informate-tab tr td{
		padding-right: 20px;
		padding-left: 20px;
		font-size: 16px;
	}
	.seeInfo .mintui{
		font-size: 20px;
	}
	.seeInfo .edit{
		text-decoration: none;
		font-size: 16px;
	}
</style>



