<template>
	<div class="smark" @click="close" >
		<div id="warp_img" >
			<img :src="picture"   @click.stop="" id="img" >
		</div>
	</div>
</template >
<script >
	export default {
		props:{
			picture:String,
			value:Boolean
		},
		
		methods:{
			close(){
				this.$emit('input', false)
			},
			
			updateImg(){

				var warp_img= document.getElementById('warp_img');
				var img= document.getElementById('img');

				//判断横竖
				if(img.offsetWidth>=img.offsetHeight){
					//横图缩短宽度
					img.style.width = warp_img.offsetWidth+"px";

				}else{
					//竖图缩短高度
					img.style.height = (warp_img.offsetHeight-200)+"px";
				}
				//居中显示
				//上下
				img.style.top = (warp_img.offsetHeight - img.offsetHeight)/2 + "px";
				// 左右
				img.style.left = (warp_img.offsetWidth - img.offsetWidth)/2 + "px";
				img.setAttribute("class","fadein")
			},
			 imgLoad(img, callback) {
	            var timer = setInterval(function() {
	                if (img.complete) {
	                    callback(img)
	                    clearInterval(timer)
	                }
	            }, 50)
        	}
       
		},
		
		mounted (){
			this.imgLoad(document.getElementById('img'),(img)=>{
				this.updateImg();
				
			})
			
			
		}
	}
</script>
<style scoped lang="scss">
	.smark{
		top: 0px;
		left: 0px;
		width: 100%;
		height: 100%;
		position: fixed;
		background: rgba(0,0,0,.8);
		z-index: 99;
		
		#warp_img{

			width: 100%;
			height:100%;
			position:relative;
			img{
				display: block;
				opacity: 0;
				border:1px solid #fff;
				position:absolute;
				transition: opacity 0.15s;
				-moz-transition: opacity 0.15s;	/* Firefox 4 */
				-webkit-transition: opacity 0.15s;	/* Safari 和 Chrome */
				-o-transition: opacity 0.15s;
			}
			
			.fadein{
				opacity: 1
			}
				
		}
		
	}
	
</style>


