<template>
	<div class="create">
		<!-- <mt-header title="家人" class="createName">
			<router-link to="/focusFamily" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->
		<div class="focus">
			<mt-field label="称呼" placeholder="请输入称呼" v-model="family.appellation"></mt-field>
			<mt-field label="微信号/会员号" placeholder="请输入手机号码" v-model="family.tel"></mt-field>
		</div>
		<div class="next">
			<mt-button class="next-btn" @click.native="submit">确定</mt-button>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				family: {
					appellation: '',
					tel: ''
				}
			}
		},
		methods:{
			submit () {
				//提交被关注人接口
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/add_focu'+location.search.substring(1).split("&")[0]);
				resource.save(this.family).then((response) => {
        			this.$toast({
        				message: '提交成功',
        				position: 'bottom'
        			});
        			setTimeout(() => {
        				this.$router.push('/focusFamily');
        			}, 1000);
				}, (response) => {
     				this.$toast(response.data.error);
				})
			}
		}
	}
</script>
<style>
.focus{
	padding: 10px;
}
</style>



