<template>
	<div>
		<mt-header fixed title="血压提醒">
  		  <router-link to="/healthData" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="" slot="right">
  			   <img src="../../assets/addicon.png" width="25" height="25" @click="open('picker3')">
  		  </router-link>
  		</mt-header>
      <div style="margin-top:49px">
      <mt-datetime-picker ref="picker3" type="time" v-model="value3" @confirm="handleChange">
    </mt-datetime-picker>
    <div style="margin-top:49px;">
      <el-row :gutter="20" style="margin:0;" v-for="item in times">
        <el-col :span="19"><p>{{item.item_attention_time}}</p></el-col>
            <el-col :span="5">
              <p><el-button size="small" class="button_2" @click="movers(item.id)">删除</el-button></p>
            </el-col>
      </el-row>
      <div class="next">
          <mt-button class="next-btn" @click="open('picker3')">添加提醒时间</mt-button>
        </div>
    </div>
	</div>
</template>
<script>
import { Indicator } from 'mint-ui';
  export default{
    data(){
       var getHours = new Date().getHours();
       var getMinutes = new Date().getMinutes();
       var current = getHours +':'+ getMinutes;
       this.getTime();
       return {
        value3:current,
        times:{}
       }
    },
    methods:{
       randomIndex: function () {
          return Math.floor(Math.random() * this.times.length);
        },
        movers(item){
          var resource = this.$resource(this.$store.state.serverIP+"/health_item_attentions/delete_health_item_attention?"+location.search.substring(1).split("&")[0]);
          resource.get({health_item_attention_id:item}).then((response)=>{
            this.getTime();
          },(error)=>{

          })
        },
        open(picker) {
          this.$refs[picker].open();
        },
        //添加提醒时间
       handleChange(value) {
          var resource = this.$resource(this.$store.state.serverIP+"/health_item_attentions/add_health_item_attention?"+location.search.substring(1).split("&")[0]);
          resource.save({item_attention_time:value,health_item_id:this.$route.params.id}).then((response)=>{
              response.data
              this.getTime();
          },(error)=>{

          })
        },
        //读取提醒时间
       getTime(){
          Indicator.open('加载中...');
          this.$route.params.id
          var resource = this.$resource(this.$store.state.serverIP+"/health_item_attentions/all_item_attentions?"+location.search.substring(1).split("&")[0]);
          resource.get({health_item_id:this.$route.params.id}).then((response)=>{
            Indicator.close();
            this.times = response.data
          },(error)=>{

          })
       }
    }
  }
</script>
<style>


</style>



