<template>
  <div class="page-tabbar" @click="isTab=false">
    <!-- 导航栏 -->
    <!-- <mt-header fixed :title="title"></mt-header> -->
    <ul class="my-tabs" v-cloak>
      <div class="" @click="isActive=true;title='我的数据'">
        <router-link to="/index" :class="{'active':isActive}" tag="li" >我的数据</router-link>
      </div>
      <div class="" @click="isActive=false;title='数据汇总'">
        <router-link to="dataSummary" :class="{'active':!isActive}" tag="li">数据汇总</router-link>
      </div>

    </ul>
    <!-- tab-container -->
    <div class="page-wrap">
      <transition>
        <router-view></router-view>
      </transition>
    </div>
    <!-- 菜单 -->
    <div class="my-menu">
      <ul>
        <li @click.stop="isTab=!isTab" :class="{'active':isTab}">
          <div class="my-menu-title">
            <span><i class="iconfont icon-jiankang"></i></span>
            <p>健康中心</p>
          </div>
          <ul class="menu" v-if="isTab">
           <router-link to="/healthData" tag="li">健康数据</router-link>
           <router-link to="/mge_caese" tag="li">管理病历</router-link>
           <router-link to="/medicine" tag="li">管理吃药</router-link>
          </ul>
        </li>
        <!-- <li>
          <div class="my-menu-title">
            <span><i class="iconfont icon-zhanghu"></i></span>
            <p></p>
          </div>
          <ul class="menu">
            <router-link to="/seeInformation" tag="li">查看信息</router-link>
            <router-link to="/changePassword" tag="li">更改密码</router-link>
            <router-link to="/" tag="li">退出</router-link>
          </ul>
        </li> -->

        <li v-on:click="goVip">
          <div class="my-menu-title">
            <span><i class="iconfont icon-zhanghu"></i></span>
            <p>会员中心</p>
          </div>
        </li>

        <router-link to="/focusFamily" tag="li">
          <div class="my-menu-title">
            <span><i class="iconfont icon-guanzhu"></i></span>
            <p>家人健康</p>
          </div>
        </router-link>
      </ul>
    </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      isTab:false,
      isActive:this.$route.path=="/index"?true:false,
      title:"我的数据"
    }
  },
  methods: {
    goVip (){
      var resource = this.$resource(this.$store.state.serverIP+'/users/vip_mark?'+location.search.substring(1).split("&")[0]);
      resource.get().then((callback) => {
        if (callback.data == 1) {
            this.$router.push('/member');
        }else{
            this.$router.push('/startRegister');
        }
      });
    }
  }

}
</script>

<style>
.my-tabs{
  /*margin-top: 40px;*/
  list-style: none;
  padding:0px;
  overflow: hidden;
  border:1px solid #e6b9ba;
  border-radius: 4px;
  height: 40px;
  margin-left: 2.5%;
  width: 95%;
  margin-bottom: 0px;
}
.my-tabs li{
  color:#323232;
  width: 50%;
  text-align: center;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
  float: left;
  background: #fff;
}
.my-tabs li.active{
  color:#fff;
  background: #b01421;
}
.el-tabs__item{
  text-align: center;
  width: 50%;
}
  .menu{
    width: 98%;
    border: 1px solid #ddd;
    background: #fff;
    position: absolute;
    bottom: 65px;
    border-radius: 2px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
    padding: 0;
  }
  .menu li{
    list-style-type:none;
    text-align: center;
    border-bottom: 1px solid rgba(204,204,204,.3);
    height: 35px;
    line-height: 35px;
  }
  .mint-tab-item{
    /*color:#323232 !important;*/
    position: relative;
  }
  .mint-tab-item:hover ul{
    display: block;
  }


  .page-tabbar {
    overflow: hidden;
    height: 100%;
}
.page-wrap {
    padding: 0 16px;
    overflow: auto;
    height: calc(100% - 140px);
}
.my-menu{
  background: #fff;
  width: 100%;
  height: 60px;
  border-top: 1px solid #e6b9ba;
  position: absolute;
  bottom: 0px;
}
.my-menu>ul{
  height: 60px;
  margin: 0px;
  list-style: none;
  padding: 0;
}
.my-menu>ul>li{
  width: 33.33%;
  position: relative;
  text-align: center;
  float: left;
}
.my-menu>ul>li:hover ul{
  display: block;
}
.my-menu .my-menu-title{
  height: 60px;
}
.my-menu .my-menu-title span{
  margin:0px;
  display: block;
  padding:0px;
  height: 34px;
  line-height: 42px;
}
.my-menu .my-menu-title p{
  margin:0px;
  display: block;
  padding:3px 0px;
  height: 20px;
  line-height: 20px;
  color: #a0a0a0;
  font-size: 12px;
}
.my-menu .my-menu-title span>i{
  font-size: 26px;
  color: #a0a0a0;

}
.my-menu>ul>li.active .my-menu-title i,.my-menu>ul>li.active .my-menu-title p{
  color: #b01421 !important;
}
</style>



