<template>
  <ul class="my-tags">
    <!-- <slot name="item"   @click="updateValue(item.value)"></slot> -->
    <li :class="{ 'is-selected': value==item.value}" v-for="item in items" @click="updateValue(item.value)">{{item.text}}</li>
  </ul>
</template>
<script>
  export default {
    props:{
      type:String,
      items:Array,
      value:String
    },
    methods:{
      updateValue (value){
        let list = this.value.split(',');
        if(list.length === 1&& list[0] == ""){
          list = [];
        }
        if(this.type=="radio"){
            this.$emit('input', value);
        }else{
          if(this.value.indexOf(value)>=0){
            const index = list.indexOf(value);
            list.splice(index,1);
            this.$emit('input', list.join(','));
          }else{
            list.push(value);
            this.$emit('input', list.join(','));
          }
        }
      }
    }
  }
</script>
<style>
.my-tags{
  padding: 0;
  list-style: none;
}
.my-tags li{
  float: left;
  padding: 5px 8px;
  font-size: 14px;
  color: #ccc;
  border: 1px solid #ccc;
  margin: 5px;
  margin-left: 0px;
  border-radius: 5px;
}
.my-tags li.is-selected {
  color:#b01421;
  border-color:#b01421;
}
</style>



