<template>
	<div>
		<mt-header fixed title="服药提醒">
  		  <router-link to="/medicine" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="" slot="right">
  			<img src="../../assets/addicon.png" width="25" height="25" @click="open('picker3')">
  		  </router-link>
  		</mt-header>
  		<div style="margin-top:49px;">
  			<!-- 选择时间 start -->
		    <mt-datetime-picker ref="picker3" type="time" v-model="value3" @confirm="handleChange">
			</mt-datetime-picker>
  			<!-- 选择时间 end -->
  			<p style="font-weight: bold;margin-left: 10px">服用{{name}}提醒</p>
  			<el-row :gutter="20" style="margin:0;" v-for="item in times">
		        <el-col :span="19"><p>{{item.medicine_attention_time}}</p></el-col>
		            <el-col :span="5">
		              <p><el-button size="small" class="button_2" @click="movers(item.id)">删除</el-button></p>
		        </el-col>
		    </el-row>
		    <div class="next" v-cloak>
				<mt-button class="next-btn" @click="open('picker3')">添加服药时间</mt-button>
			</div>
  		</div>
	</div>
</template>
<script>
	export default{
		data(){
			var getHours = new Date().getHours();
	       	var getMinutes = new Date().getMinutes();
	       	var current = getHours +':'+ getMinutes;
	       	this.getMedicine();
	       	return {
	        	value3:current,
	        	name:this.$route.params.name,
	        	times:[]
	       	}
		},
		methods:{
		  randomIndex: function () {
             return Math.floor(Math.random() * this.times.length);
          },
          //读取服药时间
          getMedicine(){
          	var resource = this.$resource(this.$store.state.serverIP +"/take_medicine_attentions/all_medicine_attentions?"+location.search.substring(1).split("&")[0]);
          	resource.get({take_medicine_management_id:this.$route.params.id}).then((response)=>{
          		this.times = response.data;
          		console.log(this.times)
          	},(error)=>{

          	})
          },
          //删除某条服药时间
          movers(item){
          	var resource = this.$resource(this.$store.state.serverIP +"/take_medicine_attentions/delete_take_medicine_attention?"+location.search.substring(1).split("&")[0]);
          	resource.get({take_medicine_attention_id:item}).then((response)=>{
          		this.getMedicine();
          	},(error)=>{

          	})
          },
		 open(picker) {
	        this.$refs[picker].open();
	      },
	      //添加服药时间
	     handleChange(value) {
	     	if((this.times.length)<this.$route.params.usage){
		     	var resource = this.$resource(this.$store.state.serverIP +"/take_medicine_attentions/add_take_medicine_attention?"+location.search.substring(1).split("&")[0]);
	          	resource.save({medicine_attention_time:value,take_medicine_management_id:this.$route.params.id}).then((response)=>{
	          		this.getMedicine();
	          	},(error)=>{

	          	})
	     	}else{
	     		this.$toast('已到药品用量上限');
	     		return
	     	}
	     	
	      }
		}
	}
</script>
<style>
	.mint-datetime{
		background: #f0f0f0;
	}
	.mint-datetime .picker-toolbar{
		position: absolute;
	    top: 271px;
	    width: 100%;
	    margin-bottom: 20px;
	}
	.picker-items{
		margin-bottom: 60px;
	}
	.mint-datetime-cancel{
		float: left;
	    width: 33%;
	    margin-left: 7%;
	    border: 1px solid #b3b3b3;
	    height: 33px;
	    line-height: 33px;
	    border-radius: 6px;
	    color: #A21A28;
	}
	.mint-datetime-confirm{
		width: 33%;
		margin-right: 7%;
	    border: 1px solid #b3b3b3;
	    height: 33px;
	    line-height: 33px;
	    border-radius: 6px;
	   background: #A21A28;
    	color: #fff;
}
	.picker-slot{
		font-size: 24px;
	}
</style>



