<template>
	<div class="member" v-cloak>
		<div class="mem-cainter">
  		<div style="background: #DA2905;padding:30px 0px;">
  			<div class="user-photo">
				 <img :src="memberList.wx_avatar" slot="center">
				 <!-- <img src="../../assets/photo.png" slot="center" class="inform-img"> -->
			</div>
			<div class="user-name">
				<p>{{memberList.truename}}</p>
			</div>
			<div style="margin:auto;overflow:hidden;width:80%;">
	  			<img src="../../assets/ss.jpg" style="width:74px;height:74px;float:left;">
	  			<p style="float:left;margin-left:10px;">会员卡号&nbsp;
	  				<span>{{memberList.user_vip_info.card_number}}</span>
	  			</p>
			</div>
  		</div>
		<div class="mem-background"></div>
		</div>
		<el-row class="el2">
	  		<el-col :span="12" class="el3">
	  			<div class="el3-text" v-on:click="seeClick">
              <i class="iconfont icon-user" style="vertical-align: middle;"></i>
		  				个人信息
	  			</div>
	  		</el-col>
	  		<el-col :span="12" class="el3">
	  			<div class="el3-text">
		  		      <span>68</span>
                积分
	  			</div>
	  		</el-col>
		</el-row>
  	</div>
</template>

<script>
export default {
	data () {
		this.getMember();
		return {
			memberList:{
				user_vip_info:{}
			}
		}
	},
	methods: {
		getMember (){
			//获取用户信息
			var resource = this.$resource(this.$store.state.serverIP+'/users/get_user_info/?'+location.search.substring(1).split("&")[0]);
			resource.get().then((response) => {
				this.memberList = response.data;
			});
		},
		seeClick (){
			window.localStorage.memberList = JSON.stringify(this.memberList);
			this.$router.push('/seeInformation');
		}
	}
}
</script>

<style scoped lang="scss">
.member .mem-cainter{
	width: 100%;
	overflow: hidden;
	color: #fff;
	font-family: "微软雅黑";
	font-size: 16px;
	margin: 0;
  .user-name{
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
  .user-photo{
    margin: 0px auto;
    height: 65px;
    width: 65px;
    border-radius: 50%;
    overflow: hidden;
    img{
      width: 65px;
      height: 65px;
    }
  }
}
.botm-cainter{
	width: 100%;
	height: 50%;
}
.el2{
	margin: 20px auto;
}
.el2 .el-col{
	height:160px;
}
.el2 .el-col div{
	margin: 0 auto;
	text-align: center;
}
.el3:first-child{
	border-right: 1px solid #ddd;
}
.el3 a{
	text-decoration: none;
	color: #000;
}
.el3 {

  .el3-text{
    padding: 40px 0px;
    height:40px;
    line-height:40px;
  	font-size: 20px;
    i{
      font-size: 20px;
      display: inline-block;
      margin-top: -6px;

    }
    span{
      font-size: 28px;
      vertical-align: middle;
    }
  }
}

.mem-background{
	height: 55px;
	background:url("./../../assets/backred_text.png") no-repeat;
	background-size: 100%;
	margin: 0;
	margin-top: -1px;
}
</style>



