<template>
	<div>
		<mt-header fixed title="服药管理">
  		  <router-link to="/index" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="" slot="right">
  			<img src="../../assets/addicon.png" width="25" height="25" @click="dialogVisible = true">
  		  </router-link>
  		</mt-header>
  		<div style="margin-top:49px;">
  			<!-- 弹框内容 -->
  			<el-dialog title="新增药品" v-model="dialogVisible" size="tiny">
			  <el-row :gutter="20" class="dialog_s">
			  	<el-col :span="8">药品名称</el-col>
			  	<el-col :span="16"><input type="text" v-model="form.name"></el-col>
			  </el-row>
			  <el-row :gutter="20" class="dialog_s">
			  	<el-col :span="8">用法</el-col>
			  	<el-col :span="16"><input type="text" style="width:30%;" v-model="form.usage">次/日</el-col>
			  </el-row>
			  <el-row :gutter="20" class="dialog_s">
			  	<el-col :span="8">用量</el-col>
			  	<el-col :span="16"><input type="text" style="width:30%;" v-model="form.dosage">片/次</el-col>
			  </el-row>
			  <span slot="footer" class="dialog-footer">
			    <el-button @click="dialogVisible = false">取 消</el-button>
			    <el-button type="primary"  @click="submit()">确 定</el-button>
			  </span>
			</el-dialog>
  			<!-- 弹框内容 end  -->
  			
  			<div class="medic_name" v-for='item in medicines'>
  				<span class='a'>{{item.name}}</span>
  				<span style="width:20%;">{{item.usage}}次/日</span>
  				<span style="width:20%;">{{item.dosage}}片/次</span>
  				<span>
  					<router-link :to="{name:'addMedicine',params:{id:item.id,name:item.name,usage:item.usage}}">
  						<el-button size="small" class="button_1">提醒</el-button>
  					</router-link>
  				</span>
  				<span>
  					<!-- <router-link :to="{name:'addMedicine',params:{id:item.id,name:item.name,usage:item.usage}}"> -->
  						<el-button size="small" class="button_1" @click="movers(item.id)">删除</el-button>
  					<!-- </router-link> -->
  				</span>
  			</div>
  			<div class="next" v-cloak>
				<mt-button class="next-btn" @click="dialogVisible = true">添加服药项目</mt-button>
			</div>
  		</div>
	</div>
</template>
<script>
import { Indicator } from 'mint-ui';

	export default{
		data(){
			this.getSubmit();
			return{
				dialogVisible:false,
				medicines:[],
				form:{}
			}
		},
		methods:{
			//读取数据列表
			getSubmit(){
				 Indicator.open('加载中...');
				var resource = this.$resource(this.$store.state.serverIP +"/take_medicine_managements/all_take_medicines?"+location.search.substring(1).split("&")[0]);
		    	resource.get({}).then((response)=>{
		    		Indicator.close();
		    		this.medicines = response.data;
		    		console.log(this.medicines)
		    	},(error)=>{

		    	})
			},
			//提交添加新数据
			submit(){
				// location.search.substring(1).split("&")[0]
				// 解析出来后: wx_id=111111111
				// debugger
				// this.form.user_id = 2;
				var resource = this.$resource(this.$store.state.serverIP+'/take_medicine_managements/add_take_medicine_management?'+location.search.substring(1).split("&")[0]);
				resource.save(this.form).then((response)=>{
					response.data
					console.log(response.data)
					this.getSubmit()
					this.dialogVisible = false;
					this.form =[];
				},(error)=>{

				})
			},
			movers(item){
	    		var resource = this.$resource(this.$store.state.serverIP+"/take_medicine_managements/delete_take_medicine_management?"+location.search.substring(1).split("&")[0]);
	    		this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: '删除成功!'
		          });
		    		resource.get({take_medicine_management_id:item}).then((response)=>{
						this.getSubmit();
		    		},(error)=>{
		    		})
		        })
	    	}
		}
	}
</script>
<style>
	.el-dialog--tiny{
		width: 90%;
	}
	.dialog_s input{
		border:1px solid #8492a6;
		margin-bottom: 20px;
		height: 30px;
		border-radius: 5px;
		margin-right: 12px;
	}
	.el-dialog__header{
		text-align: center;
	}
	.el-dialog__footer{
		text-align: center;
	}
	.medic_name{
		margin-right: 0;
		border-bottom:1px solid rgb(234, 234, 234);
		padding:15px 0px;
		width: 100%;
	}
	.medic_name>span{
		display: inline-block;
	}
	.medic_name .a{
		width:25%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
</style>


