<template>
	<div >
		<mt-header fixed title="管理病例">
  		  <router-link to="/index" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="/uploder" slot="right">
  			<img src="../../assets/addicon.png" width="25" height="25">
  		  </router-link>
  		</mt-header>
		<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
  		<div style="margin-top:49px;width:98%;margin-left:1%;">
			<div>
				<input type="search" class="search_ipt" v-model="searchName" @change="changeSearch(searchName)"><span>搜索</span>
			</div>
			<p><img src="../../assets/hot.png" alt=""style='vertical-align: middle;'>关键字</p>
			<div>
				<button v-for="button in buttons" class="btn_s" @click="changeButton(button.name)">{{button.name}}</button>
			</div>
			<!-- 新增按钮 -->
			<div class="next" style="margin-top: 15px;">
				<router-link to="/uploder" slot="right">
					<mt-button class="next-btn" @click="dialogVisible = true" style="border:1px solid #A21A28;background:#fff;color:#101010;"><span style="font-size:20px;color:#737373;">+</span>添加病例</mt-button>
  		  		</router-link>
			</div>
			<hr>
			<div class="warp-scroll">
				<div v-for='group in groups' class="group_name" v-if="state">
					<div>{{group.record_date}}</div>
					<div class="item_name">
						<div v-for='item in group.record_content'>
							<img v-bind:src="path+item.thumb_image_path" @click="scale(path+item.image_path)">
							<div style="margin-top:5px;">{{item.name}}</div>
						</div>
					</div>
				</div>
				<div v-for='list in btnList || searchList' class="group_name" v-if="dispare">
					<div>{{list.record_date}}</div>
					<div class="item_name">
						<div v-for="item in list.record_content">
							<img v-bind:src="path+item.thumb_image_path" @click="scale(path+item.image_path)">
							<div style="margin-top:5px;">{{item.name}}</div>
						</div>
					</div>
				</div>
				<div v-if="tag"><p>没有相符条件</p></div>
			</div>
			<!-- end -->
			<div class="next" style="margin-top:15px;">
				<mt-button class="next-btn" @click="loadeMore()" style="border:1px solid #A21A28;background:#fff;color:#101010;">加载更多<img src="../../assets/down.png" style="vertical-align:middle;"></mt-button>
			</div>
		</div>
		<flexImg :picture="picture"  v-if="isClose" v-model="isClose"></flexImg>	
	</div>
</template>
<script>
import flexImg from "./flexImg"

	export default{
		components:{flexImg},
		data(){
			var buttons = [{name:'体检报告'},{name:'CT'},{name:'B超'},{name:'血常规'}];
			this.getKill();
			return {
				searchName:'',
				value:null,
				buttons:buttons,
				sheetVisible: false,
				actions: [],
				groups:[],
				isClose:false,
				path: this.$store.state.serverJS,
				picture:'',
				searchList:[],
				btnList: [],
				state:true,
				tag:false,
				page:1
			}
		},
		computed:{
			dispare (){
				if(this.btnList.length <=0){

					return this.searchList.length<=0?false:true;
				}else{
					return this.btnList.length<=0?false:true;
				}

			}
		},
		methods:{
			getKill(){
				var page =1;
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/all_medical_records?"+location.search.substring(1).split("&")[0]);
				resource.get({per_page:2,page:page}).then((response)=>{
					this.groups = response.data;
				},(error)=>{

				})
			},
			loadeMore(){
				this.page +=1;
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/all_medical_records?"+location.search.substring(1).split("&")[0]);
				resource.get({per_page:2,page:this.page}).then((response)=>{
					this.groups=this.groups.concat(response.data);
					if(response.data ==''){
						this.$toast('没有更多数据了');
					}else{
					}
				})
			},
			scale(url){
				this.isClose=true;
				this.picture = url
				
			},
			//搜索框搜索
			changeSearch(search){
				this.state = false;
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/search/");
				resource.get({name:search}).then((response)=>{
					this.searchList=response.data
					if(this.searchList.length<=0){
						this.tag = true;
					}else{
						this.tag = false;
					}
					if(search==""){
						this.searchList=[];
						this.state = true;
						this.tag = false;
					}
				})
			},

			//按钮搜索
			changeButton(btn_name){
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/search_two/");
				resource.get({name:btn_name}).then((response)=>{
					this.btnList=response.data
					if(this.btnList.length<=0){
						this.btnList=''
						this.$toast('查无相关数据');
					}else{
						this.state = false;
					}
				})
			},

			// calce(){
			// 	document.getElementById("smark").style.display ="none";
			// }
		},
	}
</script>
<style scoped>
	.mint-searchbar-core{
		width: 100%;
	}
	.mint-search{
		height: 9vh !important;
	}
	li{
		list-style: none;
	}
	.btn_s{
		margin-right: 3%;
		border-radius: 4px;
		background: #9A9A9A;
		color: #fff;
		height: 41px;
		padding: 2% 5%;
		border: none;
	}
	.btn_s:hover{
		background: #A21A28;
		border: none;
	}
	.group_name{
		/*margin: 10px 10px;*/
        font-size: 20px;
        z-index: 1;
	}
	.item_name{
		font-size: 16px;
		margin-top: 12px;
	}
	.item_name div{
		display: inline-block;
		width: 24%;
		margin-right: 5px;
	}
	.item_name div>div{
		width:100%;
		line-height:17px;
		text-align:center;
	}
	.item_name img{
		width:100%;
		height:80px;
		border-radius:5px;
		/*border:1px solid #000;*/
	}
	.search_ipt{
		width:80%;
		background:#DEDEDE;
		display:inline-block;
		border: none;
		border-radius:4px;
		padding:8px;
		margin-right:12px;
		margin-right:6px
	}
	#smark{
		background: rgba(0,0,0,.8);
		position: absolute;
		z-index: 99;
		display: none;
		top:0;
		left:0;
		bottom:0;
	}
	#smark>img{
		width: 50%;
		height: 50%;
		border: none;
		position: relative;
		margin-top: 22%;
	}
	.warp-scroll {
		overflow: auto;
		height:calc(100vh - 48vh)
	}
</style>


