<template>
	  <div>
	  	<div>
	  		<!-- <mt-header fixed title="健康数据">
	  		  <router-link to="/" slot="left">
	  		    <mt-button icon="back"></mt-button>
	  		  </router-link>
	  		  <router-link to="" slot="right">
	  			<img src="../../assets/addicon.png" width="25" height="25" @click="dialogVisible = true">
	  		  </router-link>
	  		</mt-header> -->
	  		<div >
	  			<!-- 弹框 添加新项目 -->
		  		<el-dialog title="添加关注项目" v-model="dialogVisible" size="tiny">
				  <el-row :gutter="20" class="dialog_s">
				  	<el-col :span="8" class="add_name">名称</el-col>
				  	<el-col :span="16"><input type="text" v-model="dataForm.name"></el-col>
				  </el-row>
				  <el-row :gutter="20" class="dialog_s">
				  	<el-col :span="8"  class="add_name">单位</el-col>
				  	<el-col :span="16"><input type="text" v-model="dataForm.unit"></el-col>
				  </el-row>
				  <el-row :gutter="20" class="dialog_s">
				  	<el-col :span="8"  class="add_name">正常值</el-col>
				  	<el-col :span="16"><input type="text" style="width:25%;" v-model="dataForm.normal_min">～<input type="text" style="width:25%;margin-left:12px;" v-model="dataForm.normal_max"></el-col>
				  </el-row>
				  <span slot="footer" class="dialog-footer">
				    <el-button @click="dialogVisible = false">取 消</el-button>
				    <el-button type="primary"  @click="submit()">确 定</el-button>
				  </span>
				</el-dialog>
	  			<!-- end -->
		  		<el-row :gutter="20" class="an_niu" style="border-bottom: 1px solid #EAEAEA;" v-for="data in healthDatas">
		  		  <el-col :span="12"><p>{{data.name}}</p></el-col>
		  		  <el-col :span="6" v-if="data.is_admin==0">
		  		  	<p><el-button size="small" class="button_2" @click="movers(data.id)">删除</el-button></p>
		  		  </el-col>
		  		 
		  		  <el-col :span="6" v-if="data.is_admin==1">
		  		  	<p><el-switch
	  		  		  on-color="#A21A28" off-color="#C0CCDA" on-text="" off-text="" @change="getCheck(data)" v-model="data.is_check">
	  		  		  
	  		  		</el-switch></p>
		  		  </el-col>
		  		   <el-col :span="6">
			  		  <router-link :to="{name:'pathrRemind',params:{id:data.id,title:data.name}}">
			  		  	<p><el-button size="small" class="button_1">提醒</el-button></p>
			  		  </router-link>
		  		  </el-col>
		  		</el-row>

		  		<div class="next" v-cloak>
					<mt-button class="next-btn" @click="dialogVisible = true">添加关注项目</mt-button>
				</div>
	  		</div>
	  	</div>
	  </div>
</template>
<script>
import { Indicator } from 'mint-ui';
	export default {
	    data() {
		  this.getdata();
	      return {
	        healthDatas:'',
	        dialogVisible: false,
	        dataForm:{}
	      }
	    },
	    methods:{
	    	 randomIndex: function () {
		      return Math.floor(Math.random() * this.healthDatas.length);
		    },
		    //查看健康列表
		    getdata(){
		    	Indicator.open('加载中...');
		    	var param = {status:0}
		    	var resource = this.$resource(this.$store.state.serverIP +"/health_items/all_health_items?"+location.search.substring(1).split("&")[0]);
		    	resource.get(param).then((response)=>{
		    		this.healthDatas = response.data;
		    		Indicator.close();
		    		// response.data.map(item=>{
		    		// 	if(typeof item.is_check ==Boolean){

		    		// 	}else{
		    		// 		item.is_check = item.is_check=="cancel"?false:true;
		    		// 	}
		    		// });
		    		console.log(this.healthDatas)
		    	},(error)=>{

		    	})
		    },
		    //选择或取消按钮
		    getCheck(item){
		    	var resource = this.$resource(this.$store.state.serverIP+'/health_items/check_item');
		    	resource.save({health_item_id:item.id,is_check:item.is_check}).then((response)=>{

		    	},(error)=>{})
		    },
		    //弹框
	    	submit(){
				// this.dataForm.user_id = 2;
	    		var resource = this.$resource(this.$store.state.serverIP +"/health_items/add_health_item?"+location.search.substring(1).split("&")[0]);
	    		resource.save(this.dataForm).then((response)=>{
	    			this.dialogVisible = false;
	    			this.getdata();
	    			this.dataForm = {};
	    		},(error)=>{
	    		})
			},
	    	//删除健康数据
	    	movers(item){
	    		var resource = this.$resource(this.$store.state.serverIP+"/health_items/delete_health_item?"+location.search.substring(1).split("&")[0]);
	    		this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: '删除成功!'
		          });
		    		resource.get({health_item_id:item}).then((response)=>{
						this.getdata();
		    		},(error)=>{
		    		})
		        })
	    	}
	    }
  	};
</script>
<style>
	.an_niu{
		width: 96%;
		margin-left: 2% !important;
	}
	.an_niu p{
		display: inline-block;
	}
	.button_1{
		color:#A21A28;
		border: 1px solid #A21A28;
	}
	.button_2{
		color:#3D3D3D;
		border: 1px solid #3D3D3D;
	}
	.el-message-box{
		width: 90%;
	}
	/*el-message-box__title{
		text-align: center;
	}*/
	.el-message-box__message{
		display: inline-block;
	}
	.el-message-box__input{
		margin-left: 20px;
    	display: inline-block;
		width: 80%;
	}
	.add_name{
		text-align: center;
	}

</style>



